html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  font-family: "Open Sans", sans-serif;
  background-color: #f0f0f0;
}

img {
  max-width: 100%;
  display: block;
}

p {
  font-size: 1.6rem;
  line-height: 1.8;
  margin: 0;
  padding: 0;
  color: #000000;
}

a {
  text-decoration: none;
  color: #000000;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

li {
  list-style: none;
}

h1,
h2,
h3 {
  margin: 0;
  line-height: 1.2;
}

h1 {
  font-size: 4.4rem;
  font-family: "Roboto", cursive;
}

h2 {
  font-size: 4rem;
  font-family: "Roboto", cursive;
}

h3 {
  font-size: 3rem;
}

.container {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto;
}

.img {
  width: 100%;
}

.section {
  padding: 2rem 0;
  overflow: hidden;
}

.notavif.notwebp .header {
  background-image: linear-gradient(to right, rgba(59, 72, 98, 0.8) 0%, rgba(59, 72, 98, 0.8) 100%), url("../img/page/header_bg.jpg");
}

.webp .header {
  background-image: linear-gradient(to right, rgba(59, 72, 98, 0.8) 0%, rgba(59, 72, 98, 0.8) 100%), url("../img/page/header_bg.webp");
}

.avif .header {
  background-image: linear-gradient(to right, rgba(59, 72, 98, 0.8) 0%, rgba(59, 72, 98, 0.8) 100%), url("../img/page/header_bg.avif");
}

.header {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  background-attachment: fixed;
  padding: 3rem;
  min-height: 60rem;
  position: relative;
}
.header__bar {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .header__bar {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header__logo {
  width: 25rem;
}
.header__content > * {
  color: #ffffff;
}
@media (min-width: 768px) {
  .header__content {
    margin-top: 10rem;
    max-width: 50rem;
  }
}
.header__heading {
  text-align: center;
}
@media (min-width: 768px) {
  .header__heading {
    text-align: left;
  }
}
.header__text, .header__img {
  display: none;
}
@media (min-width: 768px) {
  .header__text, .header__img {
    display: block;
  }
}
@media (min-width: 768px) {
  .header__img {
    max-width: 35rem;
    position: absolute;
    right: 0;
    bottom: -5rem;
    border-radius: 2rem 0 0 5rem;
  }
}
@media (min-width: 1024px) {
  .header__img {
    max-width: 50rem;
  }
}
@media (min-width: 1440px) {
  .header__img {
    max-width: 60rem;
  }
}

.nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 5rem;
}
.nav__link {
  font-size: 1.8rem;
  margin-bottom: 2rem;
  transition: color 0.2s ease;
  color: #ffffff;
}
.nav__link:last-of-type {
  margin-bottom: 0;
}
.nav__link:hover {
  color: #eeefed;
}
@media (min-width: 768px) {
  .nav__link {
    margin-bottom: 0;
  }
}
@media (min-width: 768px) {
  .nav {
    flex-direction: row;
    gap: 2rem;
    margin-top: 0;
  }
}

.notavif.notwebp .featured {
  background-image: linear-gradient(to bottom, transparent 0%, transparent 50%, #3b4862 50%, #3b4862 100%), url("../img/page/destacada.jpg");
}
@media (min-width: 768px) {
  .notavif.notwebp .featured {
    background-image: linear-gradient(to right, transparent 0%, transparent 50%, #3b4862 50%, #3b4862 100%), url("../img/page/destacada.jpg");
  }
}

.webp .featured {
  background-image: linear-gradient(to bottom, transparent 0%, transparent 50%, #3b4862 50%, #3b4862 100%), url("../img/page/destacada.webp");
}
@media (min-width: 768px) {
  .webp .featured {
    background-image: linear-gradient(to right, transparent 0%, transparent 50%, #3b4862 50%, #3b4862 100%), url("../img/page/destacada.webp");
  }
}

.avif .featured {
  background-image: linear-gradient(to bottom, transparent 0%, transparent 50%, #3b4862 50%, #3b4862 100%), url("../img/page/destacada.avif");
}
@media (min-width: 768px) {
  .avif .featured {
    background-image: linear-gradient(to right, transparent 0%, transparent 50%, #3b4862 50%, #3b4862 100%), url("../img/page/destacada.avif");
  }
}

.featured {
  margin-top: 5rem;
  background-repeat: no-repeat;
  background-size: 100%, cover;
  background-position: center, -50rem -30rem;
}
.featured__grid {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
}
@media (min-width: 768px) {
  .featured__grid {
    grid-template-rows: unset;
    grid-template-columns: repeat(2, 1fr);
  }
}
.featured__content {
  padding: 5rem;
  grid-row: 2/3;
}
.featured__content > * {
  color: #ffffff;
}
@media (min-width: 768px) {
  .featured__content {
    grid-column: 2/3;
    padding: 10rem 5rem;
  }
}
.featured__heading {
  text-transform: uppercase;
}
.featured__text {
  font-size: 1.8rem;
}
.featured__price {
  font-size: 2.4rem;
  font-weight: 700;
}
.featured__link {
  font-size: 2rem;
  padding: 1.5rem 4rem;
  border: none;
  font-weight: 700;
  display: inline-block;
  margin-top: 2rem;
  cursor: pointer;
  transition: all 0.2s ease;
  text-align: center;
  color: #3b4862;
  background-color: #ffffff;
  text-transform: uppercase;
}
.featured__link:hover {
  background-color: #e6e6e6;
}
.featured__link:focus-visible {
  outline: none;
}
@media (min-width: 768px) {
  .featured__link {
    width: auto;
  }
}
@media (min-width: 768px) {
  .featured {
    background-position: 50%, -30rem;
  }
}
@media (min-width: 1024px) {
  .featured {
    background-position: 50%, -40rem;
  }
}

.feed {
  margin-top: 5rem;
}
.feed__heading {
  font-weight: 400;
}
.feed__heading span {
  font-weight: 700;
  text-transform: capitalize;
  display: block;
}
.feed__heading::after {
  content: "";
  display: block;
  height: 1rem;
  width: 15rem;
  margin-top: 1rem;
  background-image: linear-gradient(to right, #3b4862 0%, #3b4862 50%, #6277a1 50%, #6277a1 100%);
}
@media (min-width: 1024px) {
  .feed {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 4rem;
  }
}

.testimonial {
  text-align: center;
}
.testimonial__img {
  width: 20rem;
  margin: 0 auto;
}
.testimonial__content {
  margin-top: -10rem;
  padding: 10rem 2rem 2rem 2rem;
  background-color: #ffffff;
}
.testimonial__name {
  font-size: 2.4rem;
  font-weight: 700;
}
.testimonial__text::before {
  content: "";
  background-image: url(../img/svg/quote.svg);
  background-repeat: no-repeat;
  background-position: center;
  display: inline-block;
  width: 4rem;
  height: 4rem;
  margin-right: 1rem;
}

@media (min-width: 1024px) {
  .feed__blog {
    grid-column: 2/4;
  }
}

.entry {
  margin-bottom: 2rem;
  background-color: #ffffff;
}
.entry__img {
  width: 100%;
  height: 30rem;
  object-fit: cover;
}
.entry__content {
  padding: 1rem;
}
.entry__metadata, .entry__comments {
  font-size: 1.4rem;
}
.entry__date {
  font-weight: 700;
}
.entry__link {
  font-size: 2rem;
  padding: 1.5rem 4rem;
  border: none;
  font-weight: 700;
  display: inline-block;
  margin-top: 2rem;
  cursor: pointer;
  transition: all 0.2s ease;
  text-align: center;
  color: #ffffff;
  background-color: #3b4862;
  display: block;
}
.entry__link:hover {
  background-color: #283142;
}
.entry__link:focus-visible {
  outline: none;
}
@media (min-width: 768px) {
  .entry__link {
    width: auto;
  }
}
@media (min-width: 768px) {
  .entry {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 2rem;
    align-items: center;
  }
  .entry__image-container {
    grid-column: 1/3;
  }
  .entry__content {
    grid-column: 3/6;
  }
  .entry__link {
    display: inline-block;
  }
}

.properties {
  margin-top: 5rem;
}
.properties__heading {
  font-weight: 400;
}
.properties__heading span {
  font-weight: 700;
  text-transform: capitalize;
}
.properties__heading::after {
  content: "";
  display: block;
  height: 1rem;
  width: 15rem;
  margin-top: 1rem;
  background-image: linear-gradient(to right, #3b4862 0%, #3b4862 50%, #6277a1 50%, #6277a1 100%);
}
.properties__grid {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
}
@media (min-width: 768px) {
  .properties__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 1024px) {
  .properties__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 768px) {
  .properties {
    margin-top: 10rem;
  }
}

.property {
  background-color: #ffffff;
}
.property:nth-child(1) .property__image-container {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 100%), url("../img/property/propiedad_1.jpg");
}
.property:nth-child(2) .property__image-container {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 100%), url("../img/property/propiedad_2.jpg");
}
.property:nth-child(3) .property__image-container {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 100%), url("../img/property/propiedad_3.jpg");
}
.property:nth-child(4) .property__image-container {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 100%), url("../img/property/propiedad_4.jpg");
}
.property:nth-child(5) .property__image-container {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 100%), url("../img/property/propiedad_5.jpg");
}
.property:nth-child(6) .property__image-container {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 100%), url("../img/property/propiedad_6.jpg");
}
.property__image-container {
  aspect-ratio: 10/6;
  background-size: auto 190%;
  position: relative;
  background-repeat: no-repeat;
}
@media (min-width: 768px) {
  .property__image-container {
    background-size: auto 150%;
  }
}
.property__name {
  position: absolute;
  left: 1rem;
  bottom: 1rem;
  font-size: 2.4rem;
  color: #ffffff;
}
.property__content {
  padding: 2rem;
}
.property__price {
  font-size: 3rem;
}
.property__link {
  font-size: 2rem;
  padding: 1.5rem 4rem;
  border: none;
  font-weight: 700;
  display: inline-block;
  margin-top: 2rem;
  cursor: pointer;
  transition: all 0.2s ease;
  text-align: center;
  color: #ffffff;
  background-color: #3b4862;
  text-transform: uppercase;
  display: block;
}
.property__link:hover {
  background-color: #283142;
}
.property__link:focus-visible {
  outline: none;
}
@media (min-width: 768px) {
  .property__link {
    width: auto;
  }
}

.footer {
  padding-top: 5rem;
  margin-top: 5rem;
  background-color: #3b4862;
}
@media (min-width: 768px) {
  .footer__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.footer__box {
  margin-bottom: 5rem;
}
.footer__box > * {
  color: #ffffff;
}
@media (min-width: 768px) {
  .footer__box {
    margin-bottom: 0;
  }
}
.footer__heading {
  font-size: 2rem;
  text-transform: uppercase;
}
.footer__nav {
  display: flex;
  flex-direction: column;
  gap: 1.6rem;
}
.footer__nav__link {
  font-size: 1.8rem;
  transition: all 0.2s ease;
  color: #ffffff;
}
.footer__nav__link:hover {
  color: #eeefed;
  text-decoration: underline;
}
.footer__copyright {
  padding: 1rem 0;
  text-align: center;
  background-color: #283142;
}
.footer__copyright p {
  font-size: 2rem;
  font-weight: 700;
  color: #ffffff;
}
@media (min-width: 768px) {
  .footer__copyright {
    margin-top: 5rem;
  }
}

.text-center {
  text-align: center;
}

.bg-primary {
  background-color: #3b4862;
}

.link-active {
  color: red;
}

.line-divisor {
  display: none;
  height: 2.5rem;
  border-right: 1px solid #f8f8f8;
}
@media (min-width: 1024px) {
  .line-divisor {
    display: block;
  }
}

[class$=__heading] {
  margin: 2rem 0;
}